import React, { useEffect, useRef, useState } from "react";
import "./Home.css";
import Navbar from "../../components/Navbar/Navbar";
import Dark_mood from "../../images/landing page images/navbar images/dark.png";
import light_mode from "../../images/landing page images/navbar images/light.png";
import About from "../../components/About/About";
import ProductList from "../../components/Prodects/ProductList";
import backgroundImage from "../../images/background/hero-bg.svg";
import backgroundImage2 from "../../images/background/hero-bg-2.jpg";
import backgroundImage3 from "../../images/background/hero-bg3.jpg";
import Footer from "../../components/Footer/Footer";
import WhatsappIcon from "../../images/WhatsApp_icon.png";
import { useTheme } from "../../context";
import { useLocation, useNavigate } from "react-router-dom";
import ScrollToTop from "../../components/ScrollToTop";
import Cookies from "js-cookie";

const Home = ({ CartPop }) => {
  const navigate = useNavigate("");
  const reservationRef = useRef();
  const slideRef = useRef();
  const { pathname } = useLocation();
  const isReservation = pathname.includes("reservation");
  console.log(isReservation);

  const [slide, setSlid] = useState(backgroundImage);
  const [red, setRed] = useState(true);
  const [red1, setRed1] = useState(false);
  const [red2, setRed2] = useState(false);

  const changeColor = () => {
    setRed(true);
    setRed1(false);
    setRed2(false);
  };
  const changeColor1 = () => {
    setRed1(true);
    setRed2(false);
    setRed(false);
  };
  const changeColor2 = () => {
    setRed2(true);
    setRed1(false);
    setRed(false);
  };




  const { darkMode, toggleDarkMode } = useTheme();
  useEffect(() => {
    if (isReservation) reservationRef.current.scrollIntoView();
  }, [isReservation]);
  // handle slider but in a very bad way => the slider must be initiated in a better way
  useEffect(() => {
    const slides = slideRef.current.querySelectorAll(":scope > span");

    let counter = 0;
    const slider = setInterval(() => {
      if (counter === slides.length) counter = 0;
      console.log(counter);
      slides[counter].click();
      let innerSpan = slides[counter].querySelector("span");
      innerSpan.click();
      counter++;
    }, 2000);
    return () => clearInterval(slider);
  }, []);
  return (
    <>
      <ScrollToTop />
      <div
        className={!CartPop ? "hero-section bluer" : "hero-section"}
        id="Home"
        style={{ backgroundImage: `url(${slide})` }}>
        {/* <Navbar /> */}
        <a href="https://wa.me/41766025572" target="_blank">
        <img src={WhatsappIcon} className="WhatsappIcon" alt="" />
        </a>
        <section className="hero-main-section">
          <div className="container">
            <div className="text">
              <h1>
                <p className="Tilte-home">Wangen </p>
                Pizza, Kebab und Kurier
              </h1>
              <h3 className="desc">
                Unser Geheimrezept: Qualität, Frische und Vielfalt!
              </h3>
              <button className="home-btn" onClick={() => navigate("/new")}>
                Jetzt bestellen
              </button>
            </div>
            {/* !darkMode ? */}
            <div className="toggle-btn">
             {darkMode ? <img
                src={Dark_mood}
                alt=""
                className="dark-image-mode"
                onClick={() => {
                  toggleDarkMode();
                }}
              />: <img
              src={light_mode}
              alt=""
              className="white-image-mode"
              onClick={() => {
                toggleDarkMode();
              }}
            />}
            </div>
          </div>
          <div className="slide-bg" ref={slideRef}>
            <span
              onClick={() => {
                setSlid(backgroundImage);
              }}
            >
              <span
                onClick={() => changeColor()}
                style={{ background: red && "#FF8C42" }}
              ></span>{" "}
            </span>
            <span
              onClick={() => {
                setSlid(backgroundImage2);
              }}
            >
              <span
                onClick={() => changeColor1()}
                style={{ background: red1 && "#FF8C42" }}
              ></span>
            </span>
            <span
              onClick={() => {
                setSlid(backgroundImage3);
              }}
            >
              <span
                onClick={() => changeColor2()}
                style={{ background: red2 && "#FF8C42" }}
              ></span>
            </span>
          </div>
        </section>
      </div>
      <About isDark={darkMode} />
      <ProductList isDark={darkMode} reservationRef={reservationRef} />
      {/* <Footer /> */}
    </>
  );
};

export default Home;
