import React from "react";
import "./section6.css";
const Section6 = () => {
  return (
    <div className="section6">
      <div className="container">
        <div className="content-section6">
          <h1>
            Im Wangen's Pizza geniessen Sie exzellente Speisen und Getränke in
            einem entspannten Ambiente.
            <br />
            Wir vereinen gekonnt die raffinierte italienische Esskultur mit dem
            Schweizer Lebensstil.
          </h1>
        </div>
      </div>
    </div>
  );
};

export default Section6;
